.analysis {
  background-color: #414141;
  text-align: center;
  color: #fff;
  padding-top: 3.125rem;

  &__title {
    font-family: "Ubuntu", sans-serif;
    font-size: 1.25rem;
    line-height: 1.875rem;
    font-weight: bold;
    padding-bottom: 3.125rem;
  }

  &__container {
    display: flex;
    margin: 0 auto;
    justify-content: space-around;
    max-width: 70%;
  }

  &__inner {
    display: flex;
    text-align: right;
    flex-direction: column;

    &--down {
      margin-top: 17px;
    }
  }

  &__field {
    background: transparent;
    padding: 0.5rem 1rem;
    border: 0;
    border-bottom: 1px solid #000;

    &--none-line {
      border: 0;
      // padding: 0;
    }
  }

  &__code {
    padding: 0.5rem 0;
    margin: 0;
  }

  &__image {
    width: 1.1rem;
    display: inline-block;
    position: relative;
    top: 4px;
  }

  &__send {
    margin-top: 2rem;
    width: 16.9375rem;
    font-size: 1.5rem;
    font-family: "Ubuntu", sans-serif;
    border: 0;
    font-weight: bold;
    padding: 1rem;
    background-color: #fff;
    transition: 0.4s;
    cursor: pointer;

    &:hover {
      background-color: #bbb;
    }
  }
}
