.nav-sub {
  background: #dfdfdf;
  padding: 2rem 0;
  &__main {
    display: flex;

    flex-wrap: wrap;
    flex-direction: row-reverse;
    justify-content: flex-end;
    position: relative;
  }

  &__link {
    font-family: "Ubuntu", sans-serif;
    font-size: 1.25rem;
    line-height: 2rem;
    font-weight: bold;
    color: #2e2e2e63;
    transition: color 0.3s ease-in-out;
    padding-left: 4rem;

    &--active {
      color: #2e2e2e;
    }

    &--roily {
      display: none;
      font-size: 1.125rem;
      line-height: 2.3125rem;
      color: #b9b9b9;
      filter: blur(3px);
      transition: filter 0.3s ease-in-out;

      &:hover {
        filter: blur(1px);
      }
    }

    &--active {
      position: relative;

      &::after {
        content: "";
        position: absolute;
        top: calc(100% + 0.5rem);
        left: 50%;
        width: 122px;
        height: 7px;
        border-radius: 10px;
        background: transparent;
        transform: translateX(-50%);
      }
    }

    &--yellow::after {
      background: linear-gradient(45deg, #ff2c00, #ffe600);
    }

    &--purple::after {
      background: linear-gradient(15deg, #9d7bff, #f275ff);
    }

    &--blue::after {
      background: linear-gradient(15deg, #8139ff, #00dbcc);
    }

    &--green::after {
      background: linear-gradient(15deg, #00dbfe, #00e91f);
    }
  }

